var document;
var $ = require('jquery');
var React = require('react');
var d = require('jsnox')(React);
var _ = require('underscore');
$(document).ready(function () {
  var settings = require('settings');
  if ($('#konzilo-social-post').length > 0) {
    var socialform = require('./socialform.js');
    var data = require('socialdata');

    if (!data.values) {
      data = [];
      data.push({
        active: [],
        publishSettings: {
          type: 'now',
          value: new Date(),
        }
      });
    } else {
      data = data.values;
    }
    React.render(
      d(socialform.SocialBoxes, {
        data: data,
        profiles: settings.profiles,
        konziloSocialNonce: settings.konziloSocialNonce
      }),
      $('#konzilo-social-post .inside')[0]
    );
  }
  else if ($('#konzilo-social-queue').length > 0) {
    var scheduleform = require('./scheduleform.js');
    var socialform = require('./socialform.js');
    var elements = [];
    var data;
    if (settings.defaults) {
      data = settings.defaults;
    }
    else {
      data = [];
      data.push({
        active: [],
        publishSettings: {
          type: 'now',
          value: new Date(),
        }
      });
    }

    if (settings.queues.length > 0) {
      var queue = _.findWhere(settings.queues, {id: parseInt(settings.queue)});
      if (!queue) {
        queue = settings.queues[0];
      }
      elements.push(d(scheduleform.editQueueForm, {
        queues: settings.queues,
        data: queue,
        action: settings.admin_url,
        nonce: settings.nonce
      }));
    }
    // elements.push(d(scheduleform.addQueueForm, {
    //   action: settings.admin_url,
    //   nonce: settings.nonce
    // }));
    elements.push(d(socialform.DefaultForm, {
      action: settings.admin_url,
      data: data,
      profiles: settings.profiles,
      konziloSocialNonce: settings.konziloSocialNonce
    }));
    React.render(
      d('div.queueForms', elements),
      $('#konzilo-social-queue')[0]
    );
  }
});



