var React = require('react');
var d = require('jsnox')(React);
var _ = require('underscore');
var t = require('translations');
var exports, hours = [], minutes = [];

for (var i = 0; i <= 59; i++) {
  var key;
  if (i < 10) {
    key = `0${i}`;
  }
  else {
    key = `${i}`;
  }
  if (i <= 24) {
    hours.push(key);
  }
  minutes.push(key);
}

exports.DayOptions = React.createClass({
  days: [t.monday, t.tuesday, t.wednesday,
         t.thursday, t.friday, t.saturday, t.sunday],
  change(e) {
    var value = parseInt(e.target.value);
    if (e.target.checked) {
      this.props.days.push(value);
    }
    else {
      this.props.days = _.without(this.props.days, value);
    }
    this.forceUpdate();
  },
  toggleAlldays(e) {
    if (e.target.checked) {
      this.props.days = [0, 1, 2, 3, 4, 5, 6];
    }
    else {
      this.props.days = [];
    }
    this.forceUpdate();
  },
  render() {
    var allDays = this.props.days.length === 7;
    var days = _.map(this.days, (day, key) => {
      var checked = _.contains(this.props.days, key);
      return d('span.day', { key: key }, [
        d('input', {
          type: 'checkbox',
          name: `days[${key}]`,
          value: key,
          checked: checked,
          day: key,
          onChange: this.change
        }),
        d('span.label', day)
      ]);
    });
    days.unshift(d('div.alldays', [
      d('input', {
        type: 'checkbox',
        name: 'alldays',
        checked: allDays,
        onChange: this.toggleAlldays
      }),
      d('span.label', t.allDays)
    ]));
    return d('tr.days', [
      d('th', t.days),
      d('td.days', days)
    ]);
  }
});

exports.TimePicker = React.createClass({
  getInitialState() {
    var items = this.props.time.split(':');
    var hour = items[0], minute = items[1];
    return { hour: hour, minute: minute };
  },
  changeHour(e) {
    this.setState({hour: e.target.value, minute: this.state.minute});
  },
  changeMinute(e) {
    this.setState({hour: this.state.hour, minute: e.target.value});
  },
  render() {
    var row = this.props.row;
    if (!this.props.time) {
      return null;
    }
    return d('span.timeparts', [
      d('select.hour', {
        name: `slots[${row}][hour]`,
        value: this.state.hour,
        onChange: this.changeHour
      }, _.map(hours, (hour) => {
        return d('option', { value: hour, key: hour }, hour);
      })),
      d('span', ':'),
      d('select.minute', {
        name: `slots[${row}][minute]`,
        value: this.state.minute,
        onChange: this.changeMinute
      }, _.map(minutes, (minute) => {
        return d('option', { value: minute, key: minute }, minute);
      }))
    ]);
  }
});

exports.TimeOptions = React.createClass({
  remove(row) {
    this.props.onRemove(row);
  },
  render() {
    var row = this.props.row;
    if (!this.props.time) {
      return null;
    }
    return d('div.time', [
      d(exports.TimePicker, {
        row: this.props.row,
        time: this.props.time
      }),
      d('button', { onClick: this.remove.bind(this, row) }, t.remove)
    ]);
  }
});

exports.QueueSchedule = React.createClass({
  addSlot(e) {
    var slots = this.props.slots;
    slots.push('00:00');
    this.forceUpdate();
    e.preventDefault();
  },
  removeSlot(row) {
    delete this.props.slots[row];
    this.forceUpdate();
  },
  render() {
    var slots = _.map(this.props.slots, (slot, key) => {
      return d(exports.TimeOptions, {
        time: slot,
        key: key,
        row: key,
        onRemove: this.removeSlot
      });
    });
    return d('tr.times', [
      d('th', t.postingTimes),
      d('td', [
        slots,
        d('button', { onClick: this.addSlot }, t.addPostingTime)
      ])
    ]);
  }
});

exports.formElement = React.createClass({
  render() {
    return d('tr', [
      d('th', this.props.name),
      d('td', this.props.content)
    ]);
  }
});

/**
 * Select the appropriate queue.
 */
exports.QueueSelect = React.createClass({
  render() {
    var options = this.props.queues.map((queue) => {
      return d('option', {
        name: 'id',
        value: queue.id,
        key: queue.id
      },queue.name);
    });
    return d(exports.formElement, {
      name: t.queue,
      content: [
        d('select', {
          name: 'id',
          onChange: this.props.onChange,
          defaultValue: this.props.queue
        }, options)
      ]
    });
  }
});

exports.editQueueForm = React.createClass({
  onChange(e) {
    this.props.data = _.findWhere(this.props.queues,
                             { id: parseInt(e.target.value) });
    this.forceUpdate();
  },
  render() {
    var data = this.props.data;
    var id = data ? data.id : null;
    return d('div.postbox', [
      d('h3.inside', t.queueSettings),
      d('form.inside', {method: 'post', action: this.props.action},[
        d('table.form-table', [
          d(exports.QueueSelect, {
            onChange: this.onChange,
            queues: this.props.queues,
            queue: id
          }),
          d(exports.DayOptions, {days: data.days}),
          d(exports.QueueSchedule, {slots: data.slots})
        ]),
        d('input', {
          key: 'name',
          type: 'hidden',
          name: 'name',
          value: data.name
        }),
        d('input', {
          key: 'edit_queue',
          type: 'hidden',
          name: 'action',
          value: 'konzilo_edit_queue'
        }),
        d('input', {
          key: 'social_nonce',
          type: 'hidden',
          name: 'social_nonce',
          value: this.props.nonce,
        }),
        d('input.button', { key: 'submit', type: 'submit', value: t.saveQueue })
      ]),
    ]);
  }
});

exports.addQueueForm = React.createClass({
  render() {
    return d('div.postbox', [
      d('h3.inside', t.createNewQueue),
      d('form.inside', { method: 'post', action: this.props.action }, [
      d('table.form-table', [
        d('tr', [
          d('th', 'Name'),
          d('td', [d('input', { type: 'text', name: 'name', required: true })])
        ]),
        d(exports.DayOptions, { days: [] }),
        d(exports.QueueSchedule, {slots: []}),
      ]),
      d('input', {
        key: 'save_queue',
        type: 'hidden',
        name: 'action',
        value: 'konzilo_save_queue',
      }),
      d('input', {
        key: 'social_nonce',
        type: 'hidden',
        name: 'social_nonce',
        value: this.props.nonce,
      }),
      d('input.button', { key: 'submit', type: 'submit', value: t.createQueue })
      ])
    ]);
    }
});



