var exports;

var React = require('react');
var d = require('jsnox')(React);
var _ = require('underscore');
var Pikaday = require('pikaday');
var scheduleform = require('./scheduleform.js');
var t = require('translations');
require('../node_modules/pikaday/css/pikaday.css');

var services = {
  facebook: 'icon-facebook-circled',
  twitter: 'icon-twitter-circled',
  linkedin: 'icon-linkedin',
  googleplus: 'icon-gplus-circled'
};


exports.AddProfile = React.createClass({
  getInitialState() {
    return { opened: false };
  },
  toggleOpen(event) {
    this.setState({opened: !this.state.opened});
    event.preventDefault();
  },
  close() {
    this.setState({opened: false});
  },
  addProfile(profile) {
    this.props.data.active.push({
      profileId: profile.id,
      text: ''
    });
    this.props.onProfileAdded(profile);
    this.setState({opened: false});
  },
  render() {
    var classes = this.state.opened ? 'dropdown' : 'dropdown hidden';
    var liClasses = this.state.opened ? 'add-profiles active' : 'add-profiles';
    var activeProfiles = _.pluck(this.props.data.active, 'profileId');
    var inArray = this.props.inArray;
    var profiles = this.props.profiles.map((profile) => {
      if (!activeProfiles.length ||
          inArray(activeProfiles, profile.id) === false) {
        return d('li', { key: profile.id }, [
          d('a', {
            href: '#${profile.profile_id}',
            onClick: this.addProfile.bind(this, profile)
          }, [
            d('i.' + services[profile.service]),
            d('span.profile-username', profile.formatted_username),
          ])
        ]);
      }
    });
    if (this.props.profiles.length > activeProfiles.length) {
      return d('li', { className: liClasses, onClick: this.toggleOpen }, [
        d('span.dashicons.dashicons-plus-alt'),
        d('span.add-profile-toggle', t.addProfile),
        d('span.dashicons.dashicons-arrow-down'),
        d('ul', { 'className': classes }, profiles)
      ]);
    } else {
      return null;
    }
  }
});


exports.SocialText = React.createClass({
  getInitialState() {
    return { text: this.props.text };
  },
  textChanged(e) {
    this.setState({ text: e.target.value });
    this.props.textChanged(this.props.profile, e.target.value);
  },
  render() {
    var elements = [
      d('textarea.widefat', {
        rows: 4,
        cols: 4,
        onChange: this.textChanged,
        name: `socialbox[${this.props.row}][${this.props.profile.profileId}]`,
        value: this.state.text,
        disabled: this.props.data.sent
      }),
      d('div.tips', [
        d('span', t.useShortCodes),
        d('code', { key: 'sitename' }, '[sitename]'),
        d('code',{ key: 'title' }, '[title]'),
        d('code',{ key: 'author' }, '[author]'),
        d('code', { key: 'date' }, '[date]'),
        d('code', { key: 'excerpt' }, '[excerpt]'),
        d('code', { key: 'pageurl' }, '[pageurl]')
      ])
    ];
    if (this.props.profile.id) {
      elements.push(d('input', {
        value: this.props.profile.id,
        name: `socialboxid[${this.props.row}][${this.props.profile.profileId}]`,
        type: 'hidden'
      }));
    }
    return d('div.konzilo-tabs-panel', elements);
  }
});

exports.Textareas = React.createClass({
  getInitialState() {
    var texts = {};
    _.each(this.props.data.active, (profile) => {
      texts[profile.profileId] = profile.text;
    });
    return { texts: texts };
  },
  textChanged(profile, value) {
    var texts = this.state.texts;
    texts[profile.profileId] = value;
  },
  render() {
    var textareas = [], txtclass;
    if (this.props.data.active.length > 0) {
      var currentTextArea = this.props.getTextarea();

      textareas = _.map(this.props.data.active, (profile, idx) => {
        if (currentTextArea === null) {
          txtclass = (idx % this.props.data.active.length === 0) ? '' : '.hidden';
        }
        else {
          txtclass = (profile.profileId === currentTextArea) ? '' : '.hidden';
        }
        return d('div.text' + txtclass, { key: 'text_' + profile.profileId }, [
          d(exports.SocialText, {
            key: idx,
            profile: profile,
            text: this.state.texts[profile.profileId],
            textChanged: this.textChanged,
            row: this.props.row,
            data: this.props.data,
          })
        ]);
      });
      return d('div.konzilo-tabs-panels', [
        textareas
      ]);
    }
    return null;
  }
});


exports.ProfileTabs = React.createClass({
  profileAdded(profile) {
    this.activateProfile(profile.id);
  },
  profileRemoved() {
    this.props.onChanged();
  },
  textChanged() {},
  removeProfile(profileId) {
    var idx = _.indexOf(this.props.data.active, (p) => {
      return p.profileId === profileId;
    }, 'profileId');
    this.props.data.active.splice(idx, 1);
    this.props.setTextarea(null);
    this.profileRemoved();
  },
  activateProfile(profileId) {
    this.props.setTextarea(profileId);
    this.props.onChanged();
  },
  render() {
    var profiles = [],
        active = this.props.data.active,
        classes;

    if (active.length > 0) {
      var currentTextArea = this.props.getTextarea();
      profiles = _.map(active, (pObj, idx) => {
        if (currentTextArea === null) {
          classes = (idx % active.length === 0) ? 'tab active' : 'tab';
        }
        else {
          classes = (pObj.profileId === currentTextArea) ? 'tab active' : 'tab';
        }

        var profile = _.find(this.props.profiles, (p) => {
          return p.id === pObj.profileId;
        }, 'id');
        // return null if we didnt find the profile anymore
        if (!profile) {
          return null;
        }
        var tabElements =  [
          d('a.profilename', {
            href: `#${pObj.profileId}`,
            onClick: this.activateProfile.bind(this, pObj.profileId)
          }, [
            d('i.' + services[profile.service]),
            d('span.profile-username', profile.formatted_username)
          ]),
        ];
        if (!this.props.data.sent) {
          tabElements.push(d('a.dashicons.dashicons-dismiss', {
            onClick: this.removeProfile.bind(this, pObj.profileId) }));
        }
        return d('li', { className: classes, key: 'active_' +  profile.id },
                 tabElements);
      });
    }
    var elements = [
      profiles
    ];
    if (!this.props.data.sent) {
      elements.push(d(exports.AddProfile, {
        profiles: this.props.profiles,
        onProfileAdded: this.profileAdded,
        inArray: this.props.inArray,
        data: this.props.data
      }));
    }
    return d('div.konzilotabs', [
      d('ul.tabs', elements)
    ]);
  }
});


/**
 *
 */
exports.Datepicker = React.createClass({
  componentDidMount() {
    var el = this.getDOMNode();
    var props = this.props;
    this.picker = new Pikaday({
      field: el,
      firstDay: props.firstDay
    });
    this.picker.setDate(props.defaultDate);
  },
  componentWillUnmount() {
    this.picker.destroy();
  },
  render() {
    if (this.picker) {
      this.picker.setDate(this.props.defaultDate);
    }
    return d('input', {
      type: 'text',
      id: 'datePicker',
      name: `socialPublishDate[${this.props.row}]`
    });
  }
});



exports.PublishSettings = React.createClass({
  handleRadioChange(event) {
    this.props.data.publishSettings.type = event.target.value;
    this.forceUpdate();
  },
  render() {
    if (this.props.data.active.length <= 0) {
      return null;
    }
    var datepicker = '',
        timepicker = '';
    if (this.props.data.publishSettings.type === 'date') {
      datepicker = d(exports.Datepicker, {
        firstDay:1,
        radiovalue: this.props.data.publishSettings.type,
        defaultDate: this.props.data.publishSettings.value,
        row: this.props.row
      });

      var dv = new Date(this.props.data.publishSettings.value);

      var appendZero = (time) => {
        return time < 10 ? '0' + time : time;
      };

      timepicker = d(scheduleform.TimePicker, {
        row: this.props.row,
        key: this.props.row,
        time: appendZero(dv.getHours()) + ':' + appendZero(dv.getMinutes())
      });
    }
    var types = {
      now: {
        text: t.publishNow
      },
      queue_first: {
        text: t.firstInQueue
      },
      queue_last: {
        text: t.lastInQueue
      },
      date: {
        text: t.scheduledAt
      }
    };
    var inputs = _.map(types, (t, key) => {
      return d('span.radios',  { key: key }, [
        d('input', {
          type: 'radio',
          name: `socialPublishSettings[${this.props.row}]`,
          value: key,
          checked: (this.props.data.publishSettings.type === key) ? true : false,
          onChange: this.handleRadioChange,
          disabled: this.props.data.sent
        }, t.text)
      ]);
    });
    return d('div.publish-settings', [
      d('div.radio-group', inputs),
      datepicker,
      timepicker
    ]);
  }
});

exports.Nonces = React.createClass({
  render() {
    var nonces = null;
    if ( this.props.konziloSocialNonce ) {
      var nonce = this.props.konziloSocialNonce;
      nonces = d('div.nonces', [
        d('input', {
          type: nonce.nonce.type,
          id: nonce.nonce.id,
          name: nonce.nonce.name,
          value: nonce.nonce.value,
          key: 'nonce_field'
        }),
        d('input', {
          type: nonce.referer.type,
          name: nonce.referer.name,
          value: nonce.referer.value,
          key: 'referer_field'
        })
      ]);
    }

    return nonces;
  }
});

/**
 * The social box component creates a box with a series of updates for
 * different profiles and a setting for when the update is going to be
 * set.
 */
exports.SocialBox = React.createClass({
  reRender() {
    this.forceUpdate();
  },
  getInitialState() {
    return { currentTextarea: null };
  },
  setcurrentTextarea(textarea) {
    this.state.currentTextarea = textarea;
  },
  getcurrentTextarea() {
    return this.state.currentTextarea;
  },
  removeBox(boxId) {
    this.props.removeBox(boxId);
  },
  inArray(haystack, value) {
    var returnvalue = false;
    haystack.forEach((h) => {
      if (h === value) {
        returnvalue = true;
      }
    });
    return returnvalue;
  },
  render() {
    var elements = [
      d('input', {
        type: 'hidden',
        key: 'socialbox_rows',
        name: `socialbox_rows[${this.props.row}]`,
        value: this.props.row
      }),
      d(exports.ProfileTabs, {
        key: 'box' + this.props.row,
        onChanged: this.reRender,
        getTextarea: this.getcurrentTextarea,
        setTextarea: this.setcurrentTextarea,
        profiles: this.props.profiles,
        row: this.props.row,
        inArray: this.inArray,
        data: this.props.data
      }),
      d(exports.Textareas, {
        key: 'area' + this.props.row,
        onChanged: this.reRender,
        getTextarea: this.getcurrentTextarea,
        setTextarea: this.setcurrentTextarea,
        profiles: this.props.profiles,
        row: this.props.row,
        inArray: this.props.inArray,
        data: this.props.data
      }),
      d(exports.PublishSettings, {
        row: this.props.row,
        data: this.props.data
      })
    ];
    if (!this.props.data.sent) {
      elements.unshift(d('a.remove-box', { onClick: this.removeBox.bind(this, this.props.row) }, [
        d('span.dashicons.dashicons-dismiss')
      ]));
    }
    if (this.props.data.sent_at) {
      var sentAt = new Date(this.props.data.sent_at),
          time = sentAt.toLocaleTimeString(),
          date = sentAt.toLocaleDateString();
      elements.push(d('div.sent-at', { key: 'sent_at' }, t.sentAt + ' ' + date
                      + ' ' + time));
    }
    if (this.props.data.id) {
      elements.push(d('input', {
        type: 'hidden',
        key: 'updateid',
        value: this.props.data.id,
        name: `updateid[${this.props.row}]`
      }));
    }
    return d('div.konzilo-social-box', elements);
  }
});

exports.SocialBoxes = React.createClass({
  reRenderAll() {
    this.forceUpdate();
  },
  addBox() {
    this.props.data.push({
      active: [],
      publishSettings: {
        type: 'now'
      }
    });
    this.reRenderAll();
  },
  removeBox(boxId) {
    this.props.data.splice(boxId, 1);
    this.reRenderAll();
  },
  render() {
    var boxes = [];
    boxes = _.map(this.props.data, (val, key) => {
      return d(exports.SocialBox, {
        profiles: this.props.profiles,
        removeBox: this.removeBox,
        data: val,
        row: key,
        key: 'box_' + key
      });
    });

    return d('div.socialBoxes', [
      boxes,
      d('a.add-new-box', { onClick: this.addBox }, [
        d('span.dashicons.dashicons-plus-alt'),
        d('span.addboxtext', t.addAnotherUpdate),
      ]),
      d(exports.Nonces, { konziloSocialNonce: this.props.konziloSocialNonce })
    ]);
  }
});

exports.DefaultForm = React.createClass({
    render() {
        return d('form.defaultForm', {
            action: this.props.action,
            method: 'post'
        }, [
          d('div.postbox', [
            d('h3.inside', t.defaults),
            d('div.inside', [
              d('input', {
                key: 'social_save_defaults',
                type: 'hidden',
                name: 'action',
                value: 'social_save_defaults'
              }),
              d(exports.SocialBoxes, {
                profiles: this.props.profiles,
                data: this.props.data,
                konziloSocialNonce: this.props.konziloSocialNonce
              }),
              d('input.button', {
                key: 'save_social_submit',
                type: 'submit',
                name: 'save_social_submit',
                value: t.saveDefaults
              }),
            ]),
          ])
        ]);
    }
});



